import {Component, OnInit} from '@angular/core';
import {TicketDto} from 'src/app/models/ticket.dto';
import {
  FormControl,
  Validators,
  FormGroup,
  FormBuilder,
} from '@angular/forms';
import {TicketService} from 'src/app/services/ticket.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {Router} from '@angular/router';

@Component({
  selector: 'app-ticket',
  templateUrl: './ticket.component.html',
  styleUrls: ['./ticket.component.scss'],
})
export class TicketComponent implements OnInit {
  ticket: TicketDto;
  ticketForm: FormGroup;
  recaptchaResponse: string | null;

  constructor(
    private readonly ticketService: TicketService,
    private readonly snackbar: MatSnackBar,
    private readonly router: Router
  ) {
    this.ticket = {};
    this.recaptchaResponse = null;
  }

  ngOnInit(): void {
    this.ticketForm = new FormBuilder().group({
      name: ['', Validators.required],
      family: ['', Validators.required],
      phone: ['', [Validators.required, Validators.pattern(/0?[1-9][0-9]{9}/)]],
      subject: ['', Validators.required],
      email: [
        '',
        [
          Validators.required,
          Validators.pattern(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g),
        ],
      ],
      content: ['', Validators.required],
    });
  }

  resolved(captchaResponse: string) {
    // console.log(this.robot);
    this.recaptchaResponse = captchaResponse;
  }

  async createTicket() {
    try {
      if (this.ticketForm.valid && this.recaptchaResponse !== null) {
        await this.ticketService.create(this.ticket).toPromise();
        this.snackbar.open('تیکت با موفقیت ارسال شد', null, {
          duration: 5000,
          verticalPosition: 'bottom',
          horizontalPosition: 'center',
        });
        this.router.navigate(['/']);
      }
    } catch (error) {
      console.log(error);
    }
  }

  get inputs() {
    return this.ticketForm.controls;
  }
}
